'use strict';

import React from 'react'
import ReactDOM from 'react-dom';


var Velocity = require('velocity-animate');

var PageContainer = React.createClass({displayName: 'PageContainer',
	getInitialState: function() {
		return {
			mounted: false,
			startStyles: {
				'translateY': '-100vh',
				'scale': 1
			},
			endStyles: {
				'translateY': 0,
				'scale': 1
			},
			easing: 'swing',
			duration: 600,
			callback: function() {

			}
		};
	},
	componentWillMount: function () {
		this.setState(this.props);
	},
	componentDidMount: function() {
		var me = this;

		// Hook styles
		for (var key in this.state.startStyles) {
			Velocity.hook(ReactDOM.findDOMNode(this), key, this.state.startStyles[key]);
		}

		this.setState({ mounted: true });
		ReactDOM.findDOMNode(this).style.display = 'block';

		var options = {
			duration: this.state.duration,
			easing: this.state.easing,
			complete: function () {
				ReactDOM.findDOMNode(me).classList.add('loaded-page');
				me.state.callback();
			}
		};

		Velocity(ReactDOM.findDOMNode(this),
			this.state.endStyles,
			options
		);
	},
	render: function () {
		var child;
		if(this.state.mounted){
			child = (React.createElement('div', null, this.props.children));
		}
		return (
			React.createElement('section', {className: 'page-content ' + this.props.className, style: {display: 'none'}},
				child
			)
		);
	}
});

module.exports = PageContainer;



